import React from 'react'
import { Button } from '../ButtonElements'
import { InfoContainer, InfoWrapper, InfoRow, Column1, Column2, 
    TextWrapper, TopLine, Heading, Subtitle, BtnWrap, ImgWrap, Img
} from './InfoElements'
import img from '../../images/img1.svg'
import python from '../../images/python.png'
import rea from '../../images/react.png'
import java from '../../images/java.png'
import mysql from '../../images/mysql.png'
import css from '../../images/css.png'
import php from '../../images/php.png'
import jspt from '../../images/jspt.png'
import cpp from '../../images/cpp.png'
import './style.css'
import img1 from '../../bgImages/1.png'
import img2 from '../../bgImages/2.png'
import img3 from '../../bgImages/3.png'
import img4 from '../../bgImages/4.png'
import img5 from '../../bgImages/5.png'
import img6 from '../../bgImages/6.png'
import img7 from '../../bgImages/7.png'
import img8 from '../../bgImages/8.png'
import img9 from '../../bgImages/9.png'
import img10 from '../../bgImages/10.png'
import img11 from '../../bgImages/11.png'
import img12 from '../../bgImages/12.png'
import './background.css'

const InfoSection = ({dark2, lightBg, imgStart, id, topLine, lightText, headline, darkText, description, buttonLabel, alt, primary, dark }) => {
    
    document.addEventListener("mousemove", parallax);
    function parallax(e) {
    this.querySelectorAll('.layer').forEach(layer => {
        const speed = layer.getAttribute('data-speed')
        const x = (window.innerWidth - e.pageX*speed)/100;
        const y = (window.innerHeight - e.pageY*speed)/100;
        layer.style.transform = `translateX(${x}px) translateY(${y}px)`
    })}
    
    return (
        <>
        
            <InfoContainer lightBg={lightBg} id={id} >
            
                <InfoWrapper>
                    
                    <InfoRow imgStart={imgStart}>
                        <Column1>
                        <TextWrapper>
                            <TopLine>{topLine}</TopLine>
                            <Heading lightText={lightText} >{headline}</Heading>
                            <Subtitle darkText={darkText}>{description}</Subtitle>
                            {/* <BtnWrap>
                                <Button to='home'
                                smooth={true}
                                duration={500}
                                spy={true}
                                exact="true"
                                offset={-80}
                                primary={primary ? 1 : 0}
                                dark = {dark ? 1: 0}
                                dark2 = {dark2 ? 1: 0}
                                >{buttonLabel}</Button>
                            </BtnWrap> */}
                        </TextWrapper>
                        <ImgWrap>
                                <Img src={img} alt={alt}/>
                        </ImgWrap>
                        </Column1>
                        <Column2>
                            <ImgWrap>

                                <div className="box">
                                    <div>
                                        <span>
                                        <h6 className="python">Python</h6>
                                        <img src={python}></img>
                                        
                                        <img className="cpp" src={cpp}></img><br />
                                        <p>C++</p>
                                        </span>
                                        <span>Java<br/>
                                        <img src={java}></img><br/>
                                        <img className="rev" src={css}></img><br/>
                                        <p>CSS</p>
                                        </span>
                                        <span>React<br/>
                                        <img src={rea}></img><br />
                                        
                                        <img className="php" src={php}></img><br />
                                        <p>PHP</p>
                                        </span>
                                        <span>Mysql<br/>
                                        <img src={mysql}></img><br />
                                        
                                        <img className="rev" src={jspt}></img><br />
                                        <p>JavaScript</p>
                                        </span>
                                    </div>
                                </div>

                            </ImgWrap>
                        </Column2>
                    </InfoRow>
                </InfoWrapper>
                <section>
            <img src={img1}  data-speed="-5" className="layer" />
            <img src={img2}  data-speed="5" className="layer" />
            <img src={img3}  data-speed="2" className="layer" />
            <img src={img4}  data-speed="6" className="layer" />
            <img src={img5}  data-speed="8" className="layer" />
            
            <img src={img7}  data-speed="4" className="layer" />
            <img src={img8}  data-speed="-9" className="layer" />
            <img src={img9}  data-speed="6" className="layer" />
            <img src={img10}  data-speed="-7" className="layer" />
            <img src={img11}  data-speed="-5" className="layer" />
            <img src={img12}  data-speed="5" className="layer" />
            
            </section>
            </InfoContainer>
            
        </>
    )
}

export default InfoSection
